___CSS_LOADER_EXPORT___.push([module.id, `.restaurant_list {
    display: grid;
    grid-row-gap: 16px;
}

/*
  responsive Baru agar tidak bingung
*/

@media screen and (min-width: 650px) {
  .app-bar {
    grid-template-columns: 1fr auto;
    padding: 8px 32px;
  }
  .app-bar .app-bar_menu {
    display: none;
  }
 
  .app-bar .app-bar_navigation {
    position: static;
    width: 100%;
  }

 
  .app-bar .app-bar_navigation ul li {
    display: inline-block;
  }
 
  .app-bar .app-bar_navigation ul li a {
    display: inline-block;
    width: 120px;
    text-align: center;
    margin: 0;
    align-items: center;
    text-decoration: none;
    
  }


}

/*
  batas baru
*/
@media screen and (max-width: 499px) {
  .nav {
    z-index: 10;
    background-color: #fff;
    width: 100%;
    position: absolute;
    

    /* This trasform moves the drawer off canvas. */
    -webkit-transform: translate(-500px, 0);
    transform: translate(-500px, 0);

    /* Optionally, we animate the drawer. */
    transition: transform 0.3s ease;
  }

  .open {
    -webkit-transform: translate(0, 0);
      transform: translate(0, 0);
  }

  .nav_item {
      display: list-item;
      border-bottom: 1px solid #e0e0e0;
      width: 100%;
      padding: 0;
      text-align: left;
      margin-left: 0;
  }

  .nav_list{
    margin-top: 150px;
    width: 100%;
  }

  header{
    margin: 0;
  }

  .header_menu {
    min-height: 44px;
    min-width: 44px;
  }

  .hero_inner {
    margin : 1rem;
    max-width: 800px;
    text-align: center;
  
  }

  .nav_item:hover  a {
    text-decoration: underline;
    padding: 0;
    border-radius: 0;
  }

  .nav_item a{
    text-decoration: none;
    min-height: 44px;
    min-width: 44px;
  }
  .headline_button a{
    min-height: 44px;
    min-width: 44px;
  }
}

@media screen and (min-width: 500px) {
  .header_menu {
    display: none;
  }
}

@media screen and (min-width: 650px) {

}

@media screen and (min-width: 700px) {
  .restaurant_list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 10px;
    grid-row-gap: 16px;
  }
}

@media screen and (min-width: 800px) {
  .nav_list {
    max-width: 800px;
    margin: 0 auto;
  }
}

@media screen and (min-width: 900px)  {
  .restaurant_list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
}

@media screen and (min-width: 1200px) {
  .main {
    max-width: 1200px;
  }
  .content {
    max-width: 1200px;
  }
}`, "",{"version":3,"sources":["webpack://./src/styles/responsive.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,kBAAkB;AACtB;;AAEA;;CAEC;;AAED;EACE;IACE,+BAA+B;IAC/B,iBAAiB;EACnB;EACA;IACE,aAAa;EACf;;EAEA;IACE,gBAAgB;IAChB,WAAW;EACb;;;EAGA;IACE,qBAAqB;EACvB;;EAEA;IACE,qBAAqB;IACrB,YAAY;IACZ,kBAAkB;IAClB,SAAS;IACT,mBAAmB;IACnB,qBAAqB;;EAEvB;;;AAGF;;AAEA;;CAEC;AACD;EACE;IACE,WAAW;IACX,sBAAsB;IACtB,WAAW;IACX,kBAAkB;;;IAGlB,+CAA+C;IAC/C,uCAAuC;IACvC,+BAA+B;;IAE/B,uCAAuC;IACvC,+BAA+B;EACjC;;EAEA;IACE,kCAAkC;MAChC,0BAA0B;EAC9B;;EAEA;MACI,kBAAkB;MAClB,gCAAgC;MAChC,WAAW;MACX,UAAU;MACV,gBAAgB;MAChB,cAAc;EAClB;;EAEA;IACE,iBAAiB;IACjB,WAAW;EACb;;EAEA;IACE,SAAS;EACX;;EAEA;IACE,gBAAgB;IAChB,eAAe;EACjB;;EAEA;IACE,aAAa;IACb,gBAAgB;IAChB,kBAAkB;;EAEpB;;EAEA;IACE,0BAA0B;IAC1B,UAAU;IACV,gBAAgB;EAClB;;EAEA;IACE,qBAAqB;IACrB,gBAAgB;IAChB,eAAe;EACjB;EACA;IACE,gBAAgB;IAChB,eAAe;EACjB;AACF;;AAEA;EACE;IACE,aAAa;EACf;AACF;;AAEA;;AAEA;;AAEA;EACE;IACE,aAAa;IACb,8BAA8B;IAC9B,qBAAqB;IACrB,kBAAkB;EACpB;AACF;;AAEA;EACE;IACE,gBAAgB;IAChB,cAAc;EAChB;AACF;;AAEA;EACE;IACE,aAAa;IACb,kCAAkC;EACpC;AACF;;AAEA;EACE;IACE,iBAAiB;EACnB;EACA;IACE,iBAAiB;EACnB;AACF","sourcesContent":[".restaurant_list {\r\n    display: grid;\r\n    grid-row-gap: 16px;\r\n}\r\n\r\n/*\r\n  responsive Baru agar tidak bingung\r\n*/\r\n\r\n@media screen and (min-width: 650px) {\r\n  .app-bar {\r\n    grid-template-columns: 1fr auto;\r\n    padding: 8px 32px;\r\n  }\r\n  .app-bar .app-bar_menu {\r\n    display: none;\r\n  }\r\n \r\n  .app-bar .app-bar_navigation {\r\n    position: static;\r\n    width: 100%;\r\n  }\r\n\r\n \r\n  .app-bar .app-bar_navigation ul li {\r\n    display: inline-block;\r\n  }\r\n \r\n  .app-bar .app-bar_navigation ul li a {\r\n    display: inline-block;\r\n    width: 120px;\r\n    text-align: center;\r\n    margin: 0;\r\n    align-items: center;\r\n    text-decoration: none;\r\n    \r\n  }\r\n\r\n\r\n}\r\n\r\n/*\r\n  batas baru\r\n*/\r\n@media screen and (max-width: 499px) {\r\n  .nav {\r\n    z-index: 1